// Platform CSS
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
@import "./variables.scss";

::-webkit-scrollbar-button {
  width: 0;
  height: 0;
  display: none;
}
::-webkit-scrollbar-corner {
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  border: 10px solid transparent;
  background-clip: padding-box;
  border-radius: 20px;
  background-color: #e1e3f4;
}
::-webkit-scrollbar-corner {
  background-color: transparent;
}
.cancel-button {
  background-color: #ffffff !important;
  color: #059eef;
  font-size: 14px;
}
.text-bold {
  font-weight: 700;
}
.font-bold,
.secondary-button-variations,
.secondary-button,
.primary-button,
.dropdown-list.vuex.dropdown-button.primary .dropdown-toggle {
  font-family: "Lato", sans-serif;
}
.color-g-11 {
  color: #ffffff;
}
.mr-2,
.mx-2 {
  margin-right: 0.5rem;
}
.p-2 {
  padding: 0.5rem;
}
.primary-button.button-disabled {
  background-image: unset;
  background-color: #059eef;
  opacity: 0.3;
  cursor: default;
}
.primary-button {
  height: 50px;
  width: 140px;
  background-image: linear-gradient(to right, #0aaefa, #0571ef);
  border-radius: 5px;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}
.bdr-rad-3-16 {
  border-radius: 0.1875em;
}
.flex-column {
  flex-direction: column;
}
.scroll-down {
  overflow-y: auto;
  overflow-x: hidden;
}
.bg-color-g-11 {
  background-color: #ffffff;
}
.bdr-color-g-7 {
  border-color: #e2e2e2;
}
.bdr-b,
.bdr-v,
.bdr {
  border-bottom: 1px solid #e2e2e2;
}
.justify-content-between {
  justify-content: space-between;
}
.align-items-center {
  align-items: center;
}
.color-7 {
  color: #191e50;
}
.cursor-pointer {
  cursor: pointer;
}
.button-container {
  height: 30px;
  border: none;
  outline: none;
  background-color: #ff5b3b;
}
.font-body-3 {
  font-size: 16px;
}
.color-7 {
  color: #191e50;
}
.font-regular,
textarea {
  font-family: Lato-Regular;
}
.checkbox-container {
  height: 18px;
  width: 18px;
}
.checkbox-checkmark {
  background-color: #ffffff;
  border: 2px solid #e1e3f4;
  border-radius: 3px;
}
.checkbox-input {
  display: none;
}
.checkbox-checkmark:after {
  content: "";
  display: block;
  position: absolute;
}
.checkbox-label {
  left: 18px;
  width: max-content;
}
.checkbox-container {
  display: block;
  width: 18px;
  height: 18px;
}
.checkbox-input:checked ~ .checkbox-checkmark {
  background-color: #059eef;
  border: 0px;
}
.checkbox-input:checked ~ .checkbox-checkmark:after {
  left: 6px;
  top: 2px;
  width: 6px;
  height: 11px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.caret {
  width: 10px;
  height: 10px;
  background-color: black;
  -webkit-mask-size: cover;
  mask-size: cover;
}
.caret-open {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  margin-top: 6px;
}
.caret-close {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
  margin-top: 0px;
}
