.model-wrapper:hover {
  box-shadow: 5px 5px 5px 5px #f0f0f2;
}

.custom-btn {
  background-color: #efefef;
  border: 1px solid #dee2e6 !important;
  width: 7.2rem;
  height: 2.5rem;
  padding: 0;
  border-radius: 0px;
  cursor: pointer;
}
.custom-btn-disabled {
  background-color: #efefef;
  opacity: 0.5;
  border: 1px solid #dee2e6 !important;
  width: 7.2rem;
  height: 2.5rem;
  padding: 0;
  border-radius: 0px;
  pointer-events: none;
}
.custom-btn-disabled:focus {
  box-shadow: none;
}
.custom-btn:focus {
  box-shadow: none;
}
.btn {
  cursor: pointer;
}
.disabled-link {
  pointer-events: none;
}
