@import "../styles/variables.scss";

.tooltip-content {
  .tippy-content {
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 12px;
    line-height: 18px;
    word-wrap: break-word;
  }

  .tippy-content,
  .tippy-arrow {
    background-color: $secondaryNavyBlue;
  }
}

.tooltip-content-types {
  .tippy-content {
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 12px;
    line-height: 18px;
    word-wrap: break-word;
  }
}

.tooltip-error {
  .tippy-arrow {
    color: $secondaryNavyBlue;
  }
  .tippy-content {
    background-color: $secondaryNavyBlue;
    color: $secondaryWhite;
  }
}
