@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap");
@import "../styles/abridged_core.scss";
@import "../styles/variables.scss";
.filters {
  font-family: "Lato", sans-serif;
  background-color: #ffffff;
  .add-filter {
    width: 90px;
    height: 40px;
    border: $borderGrey;
    border-radius: 5px;
    color: $secondaryPurple;
  }
  .modal {
    position: fixed !important;
  }
  .filters-active {
    background-color: $secondaryWhiteLilac;
  }

  .filters-modal {
    display: block !important;

    .dropdown-menu {
      z-index: 1;
    }
  }

  .filters-multiselect {
    height: 50px;
  }

  .modal-content {
    height: 100% !important;
    width: 28% !important;
    float: right;
    max-width: none;
    max-height: none;
    display: inline-table;
    .full-content {
      width: 100% !important;
      height: 100% !important;
    }
  }

  .modal-footer {
    border: none;
  }

  .filters-modal .modal-close-icon {
    top: 22px;
  }

  .list-tag {
    height: 30px;
    border-radius: 5px;
    background-color: $secondaryWhiteLilac;
  }

  .filter-tag-close {
    width: 12px;
    height: 12px;
    background-color: $secondaryPurple;
    // mask-image: url("/assets/icons/close.svg");
    // -webkit-mask-image: url("/assets/icons/close.svg");
    -webkit-mask-size: cover;
    mask-size: cover;
  }

  .filters-input {
    height: 50px;
    border: $borderGrey;
    border-radius: 5px;
    width: 50%;
    padding: 10px;
    outline: 0px;
  }
}

.filters-help-text {
  margin: 1px -3px;

  .tippy-content {
    border-radius: 5px;
    padding: 10px 15px;
  }

  .tippy-content,
  .tippy-arrow {
    background-color: $secondaryNavyBlue;
  }
}

.cancel-button {
  background-color: #ffffff !important;
  color: $secondaryBlue;
  font-size: 14px;
}
.font-body-3 {
  font-size: 16px;
}

.modal {
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1050;
}
