@import "../styles/variables.scss";

.pagination-panel {
  color: $secondaryPurple;

  .dropdown-list.vuex {
    border-color: $secondaryWhiteLilac;
    .dropdown-toggle {
      padding: 11px 0;
      border-radius: 5px;
      background-color: transparent;
      padding-top: 0.75rem;
      padding-bottom: 0.75rem;
      padding-right: 0.5rem;
      border-radius: 3px;

      .caret {
        background-color: $secondaryPurple;
      }
    }
  }

  .pagination-wrapper {
    padding: 20px;
    .pagination-button {
      width: 40px;
      height: 40px;
      background: #ffffff;
      border-radius: 3px;
      color: $secondaryPurple;
      cursor: pointer;
      &:not(:first-child) {
        margin-left: 10px;
      }
      &.ellipses {
        border: none;
      }
      &.ellipses:hover {
        border: none;
        background-color: #f6f7fc;
      }
    }
    .number {
      &-selected {
        background-color: $secondaryBlue;
        color: white;
      }
      &:hover {
        background-color: #f6f7fc;
      }
    }
    .icon {
      border: $borderGrey;
      &-selected {
        border: 1px solid #a4a5b5;
        background-color: #f6f7fc;
      }
      &:hover {
        border: $borderGrey;
        background-color: #f6f7fc;
      }
    }
    .ellipses {
      border: $borderGrey;
      &:hover {
        background-color: #ffffff;
      }
    }
    .disabled {
      opacity: 0.7;
      &:hover {
        background-color: #ffffff;
      }
    }
  }
}
