.searchbox{
    width: 300px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #E2E2E2;
    border-radius: 5px;
    .searchbox-input {
        border: none;
        outline: none;
        width: 80%;
    }
    .searchbox--search-clear {
        cursor: pointer;
        .icon{
            background: #b7b6b6;
        }
    }
    .searchbox--search-submit {
        margin-left: 5px ;
        cursor: pointer;
        .c-icon {
            width: 1.3em;
            height: 1.3em;
            .icon{
                background: #b7b6b6;
            }
        }
    }

}