@import "../styles/variables.scss";

.multiselect.vuex {
  height: 50px;
  width: 100%;

  .grey-margin {
    border-bottom: 1px solid $secondaryWhiteLilac;
    width: 100%;
  }

  .dropdown-item {
    width: 100%;
    font-family: $fontFamily;
    &:hover {
      background-color: rgba(225, 227, 244, 0.3);
    }
  }
  .dropdown-toggle {
    border: $borderGrey;
    border-radius: 5px;
  }
  .dropdown-selected {
    color: $secondaryBlue;
    background-color: #059eef1a !important;
  }
  .dropdown-menu {
    z-index: 3 !important;
    margin-top: -5px;
    border: none !important;
    border-radius: 3px;
    -webkit-box-shadow: 1px 0px 10px -1px rgba(197, 196, 196, 0.6);
    box-shadow: 1px 0px 10px -1px rgba(197, 196, 196, 0.6);
    padding: 10px 0px;
    .multi-select-list-wrapper {
      width: 100%;
    }
  }
  .checkbox-container {
    display: flex;
    align-items: center;
  }
  .search-container {
    border: $borderGrey;
    border-radius: 3px;
  }
  .multi-select-search-box {
    font-family: $fontFamily;
    font-style: italic;
    border: none;
    color: #b9b9b9;
    width: 90%;
    height: 35px;
    box-shadow: none;
    outline: none;
    font-size: 12px;
    padding: 5px;
    background-color: #fff;
    input {
      font-style: italic;
      color: $secondaryPurple;
      &::placeholder {
        opacity: 0.5;
      }
    }
  }
  .multi-select-search-box.search-box-active {
    border-color: $secondaryWhiteLilac;
  }
  .caret {
    background-color: $secondaryPurple;
  }
  .multi-select-list-wrapper {
    overflow-y: auto;
  }
  .apply-button {
    height: 50px;
    border-top: $borderGrey;
  }
  .empty-results {
    color: #b9b9b9;
    font-size: 12px;
  }
}

.multiselect.dropdown-disabled {
  background-color: $secondaryWhite;
}
