@import "../styles/variables.scss";
@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

.dropdown-list.vuex {
  font-family: "Lato", sans-serif;
  height: 50px;
  width: 100%;
  position: relative;

  .grey-margin {
    border-bottom: 1px solid $secondaryWhiteLilac;
    width: 100%;
  }

  .dropdown-item {
    font-family: $fontFamily;
    color: $secondaryNavyBlue;
    &:hover {
      background-color: rgba(225, 227, 244, 0.3);
    }
  }
  .dropdown-toggle {
    border: $borderGrey;
    border-radius: 3px;
    color: $secondaryPurple;
  }
  .dropdown-selected {
    color: $secondaryBlue;
    background-color: #059eef1a !important;
    .c-icon {
      span {
        background-color: $secondaryBlue;
      }
    }
  }
  .dropdown-menu {
    margin-top: -5px;
    border: none !important;
    border-radius: 3px;
    -webkit-box-shadow: 1px 0px 10px -1px rgba(197, 196, 196, 0.6);
    box-shadow: 1px 0px 10px -1px rgba(197, 196, 196, 0.6);
  }
  .selected-item {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .caret {
    background-color: $secondaryPurple;
  }
  .dropdown-item-disabled {
    opacity: 0.5;
    cursor: default;
  }
  .search-container {
    border: $borderGrey;
    border-radius: 3px;
  }
  .dropdown-list-search-box {
    font-family: $fontFamily;
    font-style: italic;
    border: none;
    color: #b9b9b9;
    width: 90%;
    height: 35px;
    box-shadow: none;
    outline: none;
    font-size: 12px;
    padding: 5px;
    background-color: #fff;
    input {
      font-style: italic;
      color: $secondaryPurple;
      &::placeholder {
        opacity: 0.5;
      }
    }
  }
  .dropdown-list-search-box.search-box-active {
    border-color: $secondaryWhiteLilac;
  }
  .dropdown-list-wrapper {
    overflow-y: auto;
    width: 100%;
  }
  .empty-results {
    color: #b9b9b9;
    font-size: 12px;
  }
}

.dropdown-list.dropdown-disabled {
  background-color: $secondaryWhite;
}
.drop-up > .dropdown-menu {
  top: auto;
  bottom: 100%;
}
